* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h3 {
  font-size: 30px;
  color: rgb(3, 219, 94);
}

#hamburger {
  color: white;
}

#header {
  font-size: 20px;
  box-sizing: border-box;
  height: 50px;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgb(38, 39, 46);
}

#menu {
  display: block;
  position: absolute;
  width: 100%;
  top: 50px;
  right: 0px;
  background: rgb(38, 39, 46);
  transition: 0.6s;
  z-index: 1000;
  height: 0px;
  visibility: hidden;
  overflow-y: hidden;
}

.link-li {
  display: block;
  padding: 1rem 0;
  margin: 0 1rem;
  border-bottom: 2px solid rgba(0, 0, 0, 0.05);
  color: white;
}

.link-li:hover {
  background: rgba(0, 0, 0, 0.05);
}

#nav.active #menu {
  height: calc(100vh - 70px);
  visibility: visible;
  overflow-y: auto;
}

#btn-mobile {
  display: flex;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  border: none;
  background: none;
  cursor: pointer;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  font-weight: bold;
}

#hamburger {
  border-top: 2px solid;
  width: 20px;
}

#hamburger::after,
#hamburger::before {
  content: '';
  display: block;
  width: 20px;
  height: 2px;
  background: currentColor;
  margin-top: 5px;
  transition: 0.3s;
  position: relative;
}

#nav.active #hamburger {
  border-top-color: transparent;
}

#nav.active #hamburger::before {
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
}

#nav.active #hamburger::after {
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
  top: -7px;
}

#link1 {
  color: rgb(3, 219, 94);
}
